import ChatCard from './ChatCard'
import SendBox  from "./SendBox"
import CustomDialog from './CustomDialog'

const ComponentList = [
    ChatCard, // 
    SendBox, // 
    CustomDialog
]
const components = {
    install (app) {
        //   
        Object.entries(ComponentList).forEach(([, v]) => {
            app.component(v.name, v)
        })
    }
}

export default components
