import VMdPreview from './v-md-editor'
import i18n from './i18n'

const pluginList = [
    VMdPreview, // markdown
    i18n // 
]
const plugins = {
    install (app) {
        //   
        Object.entries(pluginList).forEach(([, v]) => {
            app.use(v)
        })
    }
}

export default plugins
