/**
 * 
 */
class TabAtom {
	constructor(title, uuid) {
		this.title = title;
		this.uuid = uuid;
	}
}

/**
 * 
 */
class Tab {
	/**
	 * 
	 * @param {Object} data -  {list: [{title: string, uuid: string}]}
	 */
	constructor(data = {}) {
		let list = [];
		
		if (data.list && Array.isArray(data.list)) {
			list = data.list;
		}

		// TabAtom
		this.list = list.map(tab => {
			return tab instanceof TabAtom ? tab : new TabAtom(tab.title, tab.uuid);
		});
	}

	/**
	 * 
	 * @param {string} title - 
	 * @param {string} uuid - 
	 */
	addTab(title, uuid) {
		this.list.unshift(new TabAtom(title, uuid));
	}

	/**
	 * 
	 * @param {string} uuid - 
	 */
	removeTab(uuid) {
		this.list = this.list.filter(tab => tab.uuid !== uuid);
	}

	/**
	 * UUID 
	 * @param {string} uuid - 
	 * @returns {string} UUID
	 */
	getPrevUuid(uuid) {
		const index = this.list.findIndex(tab => tab.uuid === uuid);
		if (index === -1 || this.list.length === 1) {
			return '';
		}
		if (index === 0) {
			return this.list[1].uuid;
		}
		return this.list[index - 1].uuid;
	}

	/**
	 * UUID
	 * @param {string} uuid - 
	 * @returns {TabAtom|undefined} undefined
	 */
	findTab(uuid) {
		return this.list.find(tab => tab.uuid === uuid);
	}

	/**
	 * 
	 * @returns {TabAtom[]} 
	 */
	getAllTabs() {
		return this.list;
	}

	/**
	 * 
	 * @returns {number} 
	 */
	getCount() {
		return this.list.length;
	}
}

export { TabAtom, Tab }; 