/**
 * @fileoverview 
 * API
 *  Vuex  localStorage 
 */
import cache from '@/util/cache'; 
import { model_list } from '@/util/config'
import bg from '@/assets/images/bg2.jpg'

const settingStorage = JSON.parse(localStorage.getItem('settingStorage')) || ''

/**
 * defaultSettings 
 *  
 */

const apiKeyMap = Object.fromEntries(
    Object.entries(model_list).map(([key, value]) => [key, value.api_key || ''])
);

const defaultSettings = {
    /********************************  ********************************/
    // 
    "theme": 'light',
    // 
    "bg": bg,
    // 
    "chat_detail": true,
    // api_key 
    "api_key_map": apiKeyMap,
    // 
    "platform": 'Xunfei_Spark',
    // 
    "memory": true,
    // 
    "model_config": { 
        // 
        type: "llm", 
        // 
        name: "Spark Lite", 
        // 
        series: "xunfei",
        // 
        version: "spark lite", 
        // 
        pre_method: "xunfei", 
        // 
        post_method: "add"
    },
    //  chat|web|rag
    "chat_type": 'chat',
    /********************************  ********************************/
    // 
    "upload_limit": 1,
    // 
    "upload_type": "image/",
    //  2MB
    "upload_size": 2,
    /********************************  ********************************/
    // chunk
    "chunk_size": 500,
    //  5MB
    "kb_file_size": 5,
    // 
    "recall_count": 3,
    // 
    "selected_repoId": '',
    // 
    "kb_enabled": false
}

const setting = {
  state: Object.keys(defaultSettings).reduce((acc, key) => {
      acc[key] = settingStorage?.[key] ?? defaultSettings[key];
      return acc;
  }, {}),

  mutations: {
      CHANGE_SETTING: (state, { key, value }) => {
          if (state.hasOwnProperty(key)) {
              state[key] = value;
              cache.local.setJSON('settingStorage', state);
          }
      }
  },

  actions: {
      changeSetting({ commit }, data) {
          commit('CHANGE_SETTING', data)
      }
  }
}

if (!settingStorage) {
  cache.local.setJSON('settingStorage', defaultSettings)
}

export default setting
