/**
 * 
 */
class QA {
    constructor(qaId, query, answer, files, responseTime, finishTime, series, modelName, modelType, recall) {
        this.qaId = qaId;
        this.query = query;
        this.answer = answer;
        this.files = files;
        this.responseTime = responseTime;
        this.finishTime = finishTime;
        this.series = series;
        this.modelName = modelName;
        this.modelType = modelType;
        this.recall = recall;
    }

    setResponse(time) {
        this.responseTime = time;
    }

    setAnswer(answer) {
        this.answer = answer;
    }

    setFinish(time) {
        this.finishTime = time;
    }
}

/**
 * 
 */
class Session {
    /**
     * 
     * @param {string} sessionId - 
     * @param {Array} [data=[]] - 
     */
    constructor(sessionId, data = []) {
        this.sessionId = sessionId;
        this.data = data.map(qa => {
            return qa instanceof QA ? qa : new QA(
                qa.qaId,
                qa.query,
                qa.answer,
                qa.files,
                qa.responseTime,
                qa.finishTime,
                qa.series,
                qa.modelName,
                qa.modelType,
                qa.recall
            );
        });
    }

    /**
     * 
     * @param {QA|Object} qa - 
     */
    addQA(qa) {
        this.data.push(qa instanceof QA ? qa : new QA(
            qa.qaId,
            qa.query, 
            qa.answer,
            qa.files,
            qa.responseTime,
            qa.finishTime,
            qa.series,
            qa.modelName,
            qa.modelType,
            qa.recall
        ));
    }

    /**
     * qaId
     * @param {string} qaId - 
     * @returns {QA|undefined} undefined
     */
    findQA(qaId) {
        return this.data.find(qa => qa.qaId === qaId);
    }

    /**
     * 
     * @param {string} qaId - 
     */
    removeQA(qaId) {
        this.data = this.data.filter(qa => qa.qaId !== qaId);
    }
}

/**
 * 
 */
class Chat {
    /**
     * 
     * @param {Object} [data={}] - 
     * @param {Array} [data.list=[]] - 
     */
    constructor(data = {}) {
        let list = [];
		
		if (data.list && Array.isArray(data.list)) {
			list = data.list;
		}

        this.list = list.map(session => {
            return session instanceof Session ? session : new Session(session.sessionId, session.data);
        });
    }

    /**
     * 
     * @param {Session} session - 
     */
    addSession(session) {
        this.list.unshift(session);
    }

    /**
     * 
     * @param {string} sessionId - 
     */
    removeSession(sessionId) {
        this.list = this.list.filter(session => session.sessionId !== sessionId);
    }

    /**
     * 
     * @param {string} sessionId - 
     * @returns {Session|undefined} undefined
     */
    findSession(sessionId) {
        return this.list.find(session => session.sessionId === sessionId);
    }
}

export { Chat, Session, QA }; 