/**
 * ()
 * @param {string} model_version - 
 * @param {string} prompt - 
 * @param {Array} history - 
 * @param {string} pre_method - 
 * @param {Array} files - 
 * @param {boolean} is_search - 
 */
function preProcess(model_version, prompt, history, pre_method, files, is_search) {
    let body = {};
    console.log(is_search);
    switch (pre_method) {
        // 
        case "text_ali":
            body = {
                model: model_version,
                input: {
                    messages: buildLlmMessage(prompt, history)
                },
                parameters: {
                    result_format: "message",
                    incremental_output: true
                }
            }
            if (is_search) {
                body.parameters.enable_search = true;
            }
            break;
        // 
        case "text_baidu":
            body = {
                model: model_version,
                messages: buildLlmMessage(prompt, history),
                stream: true
            }
            if (is_search) {
                body.web_search = {
                    enable: true
                };
            }
            break;
        // 
        case "text_xunfei":
            body = {
                parameter: {
                    chat: {
                        domain: "general",
                        temperature: 0.5,
                        max_tokens: 1024,
                    },
                },
                payload: {
                    message: {
                        text: buildLlmMessage(prompt, history)
                    },
                },
            }
            break;
        // AI
        case "text_moonshot_zhipu":
            body = {
                model: model_version,
                messages: buildLlmMessage(prompt, history),
                stream: true
            }
            break;
        // 
        case "img_ali":
            body = {
                model: model_version,
                input: {
                    messages: buildAliVLMessage(prompt, history, files)
                },
                parameters: {
                    result_format: "message",
                    incremental_output: true
                }
            }
            break;
        // AI
        case "img_zhipu":
            body = {
                model: model_version,
                messages: buildZhipuVLMessage(prompt, history, files),
                stream: true
            }
            break;
        // AI
        case "igm_zhipu":
            body = {
                model: model_version,
                prompt: prompt
            }
            break;
    }
    return body;
}

/**
 * LLM
 * @param {string} prompt - 
 * @param {Array} history - 
 */
function buildLlmMessage(prompt, history) {
    function getHistory(history) {
        const array = [];
        //  history
        for (let i = 0; i < history.length - 1; i++) {
            const chat = history[i];
            array.push({
                "role": "user",
                "content": chat.query
            });
            array.push({
                "role": "assistant",
                "content": chat.answer
            });
        }
        return array;
    }
    let arr = getHistory(history)
    arr.push({
        "role": "user",
        "content": prompt
    })
    return arr;
}

/**
 * AI
 * @param {string} prompt - 
 * @param {Array} history - 
 * @param {Array} files - base64
 */
function buildZhipuVLMessage(prompt, history, files) {
    function getHistory(history) {
        const array = [];
        //  history
        for (let i = 0; i < history.length - 1; i++) {
            const chat = history[i];
            array.push({
                "role": "user",
                "content": { "type": "text", "text": chat.query }
            });
            array.push({
                "role": "assistant",
                "content": { "type": "text", "text": chat.answer }
            });
        }
        return array;
    }
    let arr = getHistory(history);
    let content = [];

    if (files && files.length > 0 && files[0].base64) {
        content.push({
            "type": "image_url",
            "image_url": {
                "url": files[0].base64
            }
        });
    }

    content.push({
        "type": "text",
        "text": prompt
    });

    arr.push({
        "role": "user",
        "content": content
    });

    return arr;
}

/**
 * 
 * @param {string} prompt - 
 * @param {Array} history - 
 * @param {Array} files - base64
 */
function buildAliVLMessage(prompt, history, files) {
    function getHistory(history) {
        const array = [];
        //  history
        for (let i = 0; i < history.length - 1; i++) {
            const chat = history[i];
            array.push({
                "role": "user",
                "content": { "text": chat.query }
            });
            array.push({
                "role": "assistant",
                "content": { "text": chat.answer }
            });
        }
        return array;
    }
    let arr = getHistory(history);
    let content = [];

    if (files && files.length > 0 && files[0].base64) {
        content.push({
            "image": files[0].base64
        });
    }

    content.push({
        "text": prompt
    });

    arr.push({
        "role": "user",
        "content": content
    });

    return arr;
}

/**
 * ()
 * @param {Object} e - API
 * @param {string} post_method - 
 */
function postProcess(e, post_method) {
    switch (post_method) {
        case "base":
            return JSON.parse(e.data).output.choices[0].message.content;
        case "text":
            const content = JSON.parse(e.data).output.choices[0].message.content;
            return content && content.length > 0 ? content[0].text : '';
        case "add":
            return e;
        case "delta":
            return JSON.parse(e.data).choices[0].delta.content;
        case "baidu":
            return JSON.parse(e.data).result;
        case "local":
            return JSON.parse(e.data).data;
        case "igm_zhipu":
            return e.data[0].url;
        default:
            console.warn(`: ${post_method}`);
            return e;
    }
}

