import store from '@/store'

export default {
    /**
     * 
     * @param {string} uuid uuid
     */
    del(uuid) {
        let tab = store.state.app.tab;
        tab.removeTab(uuid);
        store.dispatch('setTab', tab);
    },

    /**
     * 
     * @param {string} title 
     * @param {string} uuid uuid
     */
    add(title, uuid) {
        let tab = store.state.app.tab;
        tab.addTab(title, uuid);
        store.dispatch('setTab', tab);
    },

    /**
     * 
     * @param {string} uuid uuid
     * @param {string} title 
     */
    upd(uuid, title) {
        let tab = store.state.app.tab;
        let targetTab = tab.findTab(uuid);
        if (targetTab) {
            targetTab.title = title;
            store.dispatch('setTab', tab);
        }
    }
}
